body {
  margin: 0;
  padding: 0;
  font-family: 'Nunito', sans-serif;
  box-sizing: border-box;
}

html {
  box-sizing: inherit;
}

*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    font-size: 62.5%;
    box-sizing: inherit;
}

.App {
  width: 1140px;
  height: 100vh;
  margin: 0 auto;
  padding-top: 5rem;
}

header {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  letter-spacing: .5px;
}

.main-title {
  font-family: 'Josefin Sans', sans-serif;
  font-size: 4.5rem;
  font-weight: 400;
  text-align: center;
  margin-bottom: 2rem;
  letter-spacing: .7px;
}

.main-pannel {
  height: 100px;
  width: 100%;
  background-color: #3B3738;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar-wrapper {
  position: relative;
}

.icon-search {
  position: absolute;
  top: .7rem;
  left: .7rem;
}

.search-bar {
  height: 3.8rem;
  width: 25rem;
  border: none;
  font-size: 2rem;
  font-weight: 300;
  padding-left: 4rem; 
}

.search-bar:focus {
  outline: none;
}

.source-wrapper {
  position: absolute;
  left: -2rem;
  top: 11rem;
  background-color: #ffffff;
  z-index: 1000;
  font-size: 1.4rem;
  box-shadow: 5px 5px 25px 0 rgba(46, 61, 73, 0.2);
}

.source-container {
  height: 30rem;
  text-align: left;
  display: flex;
  flex-flow: column wrap;
  padding: 1rem 1rem 2rem 1rem;
  border-radius: 5px;
  width: 118rem;
  overflow: hidden;
  border: 1px solid #E0DDD9;
  position: relative;
}

.source-carousel {
  height: 26rem;
  width: 380rem;
  font-size: 2rem;
  padding: 1rem 1rem;
  transition: all .2s;
  position: absolute;
  left: 0;
}

.checkbox-container {
  position: relative;
  display: inline-block;
  width: 16.7rem;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-right: 5px;
  overflow: hidden;
  height: 2.2rem;
  text-align: left;
  margin-bottom: 5px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}

.checkbox {
  visibility: hidden;
  margin-right: 1.5rem;
  z-index: 1000;
}

.checkbox:checked ~ .checkmark {
  background-color: #c4dff6;
  border: 1px solid #c4dff6;
}

.checkmark {
  display: block;
  width: 1.8rem;
  height: 1.8rem;
  border: 1px solid #E0DDD9;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 5px;
}

.checkmark:after {
  content: '';
  top: 1.2px;
  left: 5px;
  width: 6px;
  height: 9px;
  border: solid white;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  position: absolute;
}

.label-icon-source {
    color: #171a1e;
    margin-right: 1rem;
}

.inactive {
  display: none;
}

.btn-submit {
  display: inline-block;
  position: absolute;
  bottom: 1rem;
  padding: 5px 5px;
  left: 1rem;
  font-size: 1.5rem;
  width: 9rem;
  height: 2.8rem;
  border: none;
  box-shadow: .5rem .5rem 2.5rem 0 rgba(46, 61, 73, 0.2);
  background-color: #3B3738;
  color: #ffffff;
  border-radius: 5px;
  cursor: pointer;
}

.btn-submit:focus {
  outline: none;
}

.icon-to-left,
.icon-to-right {
  font-size: 4rem;
  display: inline-block;
  color: #3B3738;
  width: 4rem;
  position: absolute;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.icon-to-left {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  left: -4.5rem;
  top: 10rem;
}

.icon-to-right {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  top: 10rem;
  right: -4.5rem;
}

.control-display-panel {
  background-color: rgba(224, 221, 217, .5);
  width: 100%;
  height: 12rem;
  display: flex;
  flex-direction: column;
  position: relative;
}

.source-selection-wrapper {
  display: flex;
  align-items: center;
  min-height: 5.5rem;
  padding-left: 2rem;
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.source-selection {
  display: flex;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  justify-self: self-start;
  text-align: left;
  width: 12.9rem;
}

.label-icon-source {
  color: #171a1e;
  margin-right: 1rem;
  font-size: 1.6rem;
}

.sources-selected {
  height: 100%;
  flex-grow: 1;
  margin-right: 2rem;
  margin-left: 2rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 1.9rem;
  letter-spacing: .7px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  flex-wrap: wrap;
}

.selected-sources {
  height: 1.7rem;
  display: flex;
  align-items: center;
  padding: .5rem;
  margin-right: .5rem;
  border-radius: 5rem;
  cursor: pointer;
}

.icon-delete {
  font-size: 1.2rem;
  cursor: pointer;
}

.calendar {
  height: 5rem;
  padding-left: 1.7rem;
  padding-top: 1rem;
  margin-bottom: 1rem;
}

.date-from,
.date-end {
  font-family: 'Nunito', sans-serif;
  font-size: 1.7rem;
  letter-spacing: .7px;
  width: 17rem;
  height: 3.5rem;
  padding: .5rem;
  border: 1px solid rgba(224, 221, 217, .5);
  border-radius: 3px;
  background-color: rgba(255, 255, 255, .7);
}

.date-from {
  margin-right: .7rem;
}

.modal-background {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .7);
  position: fixed;
  top: 0;
  left: 0;
}

.modal-wrapper {
  position: relative;
  height: 100%;
}

.modal {
  position: absolute;
  width: 95rem;
  height: 45rem;
  background-color: #ffffff;
  top: 45%;
  left: 48%;
  -webkit-transform: translate(-45%, -45%);
          transform: translate(-45%, -45%);
  border-radius: 5px;
  padding-left: 0;
  display: flex;
  flex-direction: row;
  overflow: hidden;
  color: #171a1e;
  font-size: 2rem;
  font-weight: 300;
  -webkit-animation: popUp .3s;
          animation: popUp .3s;
}

.icon-modal-close {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  width: 2rem;
  height: 2rem;
  color: #ffffff;
  position: absolute;
  right: 2.5rem;
  top: 2rem;
  font-size: 2.5rem;
}

.popup-info {
  display: flex;
  flex-direction: column;
  border-right: 1px solid #E0DDD9;
  font-size: 2rem;
}

.popup-img {
  width: 28.5rem;
  height: 18.5rem;
  margin: 4rem 1rem 3rem 1rem;
}

.popup-info span {
  margin-left: 2rem;
  margin-bottom: 7px;
  font-size: 1.3rem;
  font-weight: 400;
}

.popup-title {
  font-family: 'Josefin Sans', sans-serif;
  font-size: 2.7rem;
  padding-left: 1.7rem;
  line-height: 4rem;
}

.popup-content {
  padding: 1rem 3rem;
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.popup-article {
  display: flex;
  margin-top: 1.5rem;
  font-size: 2.3rem;
  line-height: 2.3rem;
}

.popup-description,
.popup-text {
  padding: 1rem 1.5rem;
  min-height: 23rem;
  width: 29rem;
  text-align: justify;
}

.popup-description {
  border-right: .6px solid #E0DDD9;
}

.popup-url {
  font-size: 2.1rem;
  -webkit-transform: translateY(15px);
          transform: translateY(15px);
}

.popup-url:link,
.popup-url:visited {
  color: #B9BCC1;
}

@-webkit-keyframes popUp {
  0% {
    -webkit-transform: translate(-45%, -45%) scale(.25);
            transform: translate(-45%, -45%) scale(.25);
  }

  100% {
    -webkit-transform: translate(-45%, -45%) scale(1);
            transform: translate(-45%, -45%) scale(1);
  }
}

@keyframes popUp {
  0% {
    -webkit-transform: translate(-45%, -45%) scale(.25);
            transform: translate(-45%, -45%) scale(.25);
  }

  100% {
    -webkit-transform: translate(-45%, -45%) scale(1);
            transform: translate(-45%, -45%) scale(1);
  }
}

.news-list-title {
  margin-top: 10rem;
}

.list-container {
  padding-top: .5rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  position: relative;
}

.list-control-pannel {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-end; 
  height: 4.3rem;
  margin-bottom: 2.5rem;
}

.icon-list,
.icon-grid {
  margin-right: 1rem;
  cursor: pointer;
}

.list {
  font-family: 'Josefin Sans', sans-serif;
  height: 19rem;
  display: flex;
  flex-direction: row;
  width: 91.5rem;
  padding: 1rem 1rem;
  box-shadow: .5rem .5rem 2.5rem 0 rgba(46,61,73,.2);
  margin-bottom: 2rem;
  border-radius: .7rem;
  transition: box-shadow .2s;
  cursor: pointer;
  align-items: center;
}

.list:hover {
  box-shadow: 2px 4px 8px 0 rgba(46,61,73,.2);
}

.list .article-info {
  width: 60rem;
  margin-right: 2.5rem;
  -webkit-transform: translateY(5px);
          transform: translateY(5px);
}

.list .article-title {
  font-size: 2rem;
  margin-bottom: 2rem;
  letter-spacing: .7px;
}

.list .article-description {
  font-weight: 300;
  font-size: 1.6rem;
  margin-bottom: 1.3rem;
  text-align: justify;
  letter-spacing: .7px;
}

.list .article-img {
  width: 30rem;
  height: 16rem;
}

.list .extra-info {
  display: flex;
  flex-direction: row;
  padding-right: 1rem;
}

.list .extra-info span {
  margin-right: 2rem;
  font-size: 1.3rem;
  color: #B9BCC1;
}

.card {
  font-family: 'Josefin Sans', sans-serif;
  width: 26.5rem;
  height: 25rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 1rem 1rem;
  box-shadow: 5px 5px 25px 0 rgba(46,61,73,.2);
  margin-bottom: 3rem;
  border-radius: .7rem;
  transition: box-shadow .2s;
  cursor: pointer;
  overflow: hidden;
  position: relative
}

.card:hover {
  box-shadow: 2px 4px 8px 0 rgba(46,61,73,.2);
}

.card .article-info {
  margin-right: 1.5rem;
  height: 6.2rem;
  background-color: #ffffff;
}

.card .article-title {
  font-size: 1.7rem;
  line-height: 2.9rem;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 25rem;
}

.card .article-description {
  display: none;
}

.card .article-img {
  display: inline-block;
  width: 30rem !important;
  height: 25rem;
  -webkit-transform: translate(-20px, 17px);
          transform: translate(-20px, 17px);
  position: absolute;
  top: 5.5rem;
  left: 0;
}

.card .extra-info {
  display: block;
}

.card .extra-info span {
  display: none;
}

.card .extra-info span:nth-child(2) {
  display: inline-block;
  margin-right: 2rem;
  font-size: 1.3rem;
  color: #B9BCC1;
}

.separator {
  font-size: 2.7rem;
  font-family: 'Josefin Sans', sans-serif;
  text-align: center;
  height: 5rem;
  width: 100% ;
  margin-top: 5rem;
  margin-bottom: 1.8rem;
  position: relative;
  overflow: hidden;
}

.separator-title {
  color: #ffffff;
  background-color: #3B3738;
  position: relative;
  display: inline-block;
  padding: 0 1rem;
  line-height: 3.8rem;
  font-size: 2.3rem;
  border-radius: .3rem;
  margin-bottom: 3rem;
  letter-spacing: 1px;
  padding-top: 2.5px;
}

.separator__holder {
  position: absolute;
  display: block;
  width: 200rem;
  top: 50%;
  border-top: 1px solid #E0DDD9;
  content: '';
}

.separator__right {
  left: 100%;
  margin-left: 1rem;
}

.separator__left {
  right: 100%;
  margin-right: 1rem;
}

.gallery {
  grid-column: full-start / full-end;
  display: grid;
  grid-template: repeat(7, 5vw) / repeat(8, 1fr);
  grid-gap: 1.5rem;
  padding: 1.5rem;
}

.gallery__item {
  cursor: pointer;
  position: relative;
  transition: all .3s ease-in;
}

.gallery__item:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}

.gallery__item--1 {
  grid-row: 1 / span 2;
  grid-column: 1 / span 2;
}

.gallery__item--2 {
  grid-row: 1 / span 3;
  grid-column: 3 / span 3;
}

.gallery__item--3 {
  grid-row: 1 / span 2;
  grid-column: 6 / 7;
}

.gallery__item--4 {
  grid-row: 1 / span 2;
  grid-column: 7 / -1
}

.gallery__item--5 {
  grid-row: 3 / span 3;
  grid-column: 1 / span 2;
}

.gallery__item--6 {
  grid-row: 4 / span 2;
  grid-column: 3 / span 2;
}

.gallery__item--7 {
  grid-row: 4 / 5;
  grid-column: 5 / 6;
}

.gallery__item--8 {
  grid-row: 3 / span 2;
  grid-column: 6 / span 2;
}

.gallery__item--9 {
  grid-row: 3 / span 3;
  grid-column: 8 / -1;
}

.gallery__item--10 {
  grid-row: 6 / span 2;
  grid-column: 1 / 2;
}

.gallery__item--11 {
  grid-row: 6 / span 2;
  grid-column: 2 / span 2;
}

.gallery__item--12 {
  grid-row: 6 / span 2;
  grid-column: 4 / 5;
}

.gallery__item--13 {
  grid-row: 5 / span 3;
  grid-column: 5 / span 3;
}

.gallery__item--14 {
  grid-row: 6 / span 2;
  grid-column: 8 / -1;
}

.gallery__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.healine-title {
  position: absolute;
  display: block;
  line-height: 3.2rem;
  align-items: center;
  text-align: left;
  padding-left: .5rem;
  bottom: 0;
  background-color: rgba(0, 0, 0, .7);
  width: 100%;
  height: 3.2rem;
  font-size: 1.3rem;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  color: white;
  letter-spacing: .5px;
}

.loader-wrapper {
  display: block;
  text-align: center;
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, .5);
  position: absolute;
  top: 0;
  left: 0;
}

.lds-ring {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
  -webkit-transform: translateY(-50px);
          transform: translateY(-50px);
}

.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 51px;
  height: 51px;
  margin: 6px;
  border: 6px solid #E0DDD9;
  border-radius: 50%;
  -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color:#E0DDD9 transparent transparent transparent;
}

.lds-ring div:nth-child(1) {
  -webkit-animation-delay: -0.45s;
          animation-delay: -0.45s;
}

.lds-ring div:nth-child(2) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}

.lds-ring div:nth-child(3) {
  -webkit-animation-delay: -0.15s;
          animation-delay: -0.15s;
}

@-webkit-keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

